import React from "react";
import clsx from "clsx";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";

import UserIcon from "@material-ui/icons/InsertEmoticon";

import useStyles from "../styles";

interface IProps {
  open: boolean;
  handleDrawerOpen: () => void;
}

export default function Appbar({ open, handleDrawerOpen }: IProps) {
  const classes = useStyles();

  return (
    <AppBar
      position="fixed"
      className={clsx(classes.appBar, {
        [classes.appBarShift]: open
      })}
    >
      <Toolbar>
        <IconButton
          color="inherit"
          aria-label="open drawer"
          onClick={handleDrawerOpen}
          edge="start"
          className={clsx(classes.menuButton, {
            [classes.hide]: open
          })}
        >
          <MenuIcon />
        </IconButton>
        <Typography variant="h6" noWrap>
          gcloud
        </Typography>
        <div className={classes.appBarGrow} />

        <IconButton color="inherit" aria-label="user menu" edge="start">
          <UserIcon />
        </IconButton>
      </Toolbar>
    </AppBar>
  );
}
