import React, { useRef, useState } from "react";
import { makeStyles, Theme, createStyles } from "@material-ui/core/styles";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";

// forms
import GeneralForm, { FormData as GeneralData } from "./forms/GeneralForm";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: "90%"
    },
    stepper: {
      background: "transparent"
    },
    backButton: {
      marginRight: theme.spacing(1)
    },
    instructions: {
      marginTop: theme.spacing(1),
      marginBottom: theme.spacing(1)
    },
    controls: {
      marginTop: theme.spacing(5)
    }
  })
);

function getSteps() {
  return [
    "Dados",
    "Detalhes",
    "Valores",
    "Estoque",
    "Fotos",
    "Fiscal",
    "Composição",
    "Fornecedores"
  ];
}

interface FormData {
  general: GeneralData;
}

export default function HorizontalLabelPositionBelowStepper() {
  const classes = useStyles();
  const [formData, setFormData] = useState<FormData>({
    general: {
      name: "",
      code: "",
      barcode: "",
      group: "",
      canChangeStock: false,
      enableInvoice: true,
      hasVariations: false,
      hasComposition: false,
      conversion: {
        unit1: "unit1",
        quantity2: 1,
        unit2: "unit2"
      }
    }
  });
  const [hasComposition, setHasComposition] = useState(false);
  const [activeStep, setActiveStep] = useState(0);
  const generalFormRef = useRef<{
    isValid: () => {};
    getData: () => GeneralData;
  }>();
  let currentRef = useRef<{ isValid: () => {}; getData: () => any }>();
  const steps = getSteps();

  currentRef = generalFormRef;

  function handleNext() {
    if (activeStep === 0) {
      if (currentRef && currentRef.current) {
        if (currentRef.current.isValid()) {
          console.log(currentRef.current.getData());
          setActiveStep(prevActiveStep => prevActiveStep + 1);
          setFormData({ ...formData, ...currentRef.current.getData() });
        }
      }
    } else {
      setActiveStep(prevActiveStep => prevActiveStep + 1);
    }
  }

  function handleBack() {
    setActiveStep(prevActiveStep => prevActiveStep - 1);
  }

  function handleReset() {
    setActiveStep(0);
  }

  function getStepContent(stepIndex: number) {
    switch (stepIndex) {
      case 0:
        return <GeneralForm ref={generalFormRef} values={formData.general} />;
      case 1:
        return "What is an ad group anyways?";
      case 2:
        return "This is the bit I really care about!";
      default:
        return "Uknown stepIndex";
    }
  }

  return (
    <div className={classes.root}>
      <Stepper
        activeStep={activeStep}
        alternativeLabel
        className={classes.stepper}
      >
        {steps.map(label => (
          <Step disabled={true} key={label}>
            <StepLabel disabled={true}>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>
      <div>
        {activeStep === steps.length ? (
          <div>
            <Typography className={classes.instructions}>
              All steps completed
            </Typography>
            <Button onClick={handleReset}>Reset</Button>
          </div>
        ) : (
          <div>
            <div className={classes.instructions}>
              {getStepContent(activeStep)}
            </div>
            <div className={classes.controls}>
              <Button
                disabled={activeStep === 0}
                onClick={handleBack}
                className={classes.backButton}
              >
                Voltar
              </Button>
              <Button variant="contained" color="primary" onClick={handleNext}>
                {activeStep === steps.length - 1 ? "Salvar" : "Próximo"}
              </Button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
