import React from "react";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import { Toolbar, Button } from "@material-ui/core";

// icons
import AddIcon from "@material-ui/icons/Add";
import ListIcon from "@material-ui/icons/List";

import { useRouter } from "../../hooks/router";

// subviews
import IndexSubView from "./views/index";
import AddSubView from "./views/add";
import Add2SubView from "./views/add2";
import DetailsSubView from "./views/details";

import { Switch, Route, Redirect } from "react-router-dom";
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    toolBar: {
      padding: 0
    }
  })
);

export default function EnhancedTable() {
  const classes = useStyles();
  const { match, history } = useRouter();

  const relativePath = (path: string) =>
    path ? `${match.url}/${path}` : `${match.url}`;

  return (
    <div>
      <Toolbar className={classes.toolBar}>
        <Button
          color="primary"
          onClick={() => history.push(relativePath("add2"))}
        >
          <AddIcon /> Novo
        </Button>

        <Button color="primary" onClick={() => history.push(relativePath(""))}>
          <ListIcon /> Listagem
        </Button>
      </Toolbar>

      <Switch>
        <Route exact={true} path={relativePath("")} component={IndexSubView} />
        <Route path={relativePath("add")} component={AddSubView} />
        <Route path={relativePath("add2")} component={Add2SubView} />
        <Route path={relativePath(":code")} component={DetailsSubView} />
        <Route render={() => <Redirect to={relativePath("")} />} />
      </Switch>
    </div>
  );
}
