import React, {
  useState,
  forwardRef,
  useRef,
  useImperativeHandle
} from "react";

// FORMIK
import { Formik, FormikProps } from "formik";

// YUP
import { string, object } from "yup";

//  MATERIAL UI
import TextField from "@material-ui/core/TextField";
import InputAdornment from "@material-ui/core/InputAdornment";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import Tooltip from "@material-ui/core/Tooltip";
import OutlinedInput from "@material-ui/core/OutlinedInput";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";

interface FormData {
  name: string;
  code: string;
  barcode: string;
  group: string;
}

const Form = forwardRef((props: FormikProps<FormData>, ref) => {
  const [groups, setGroups] = useState<string[]>(["CARREGANDO"]);
  const inputLabel = React.useRef<HTMLLabelElement>(null);

  const {
    values: { name, code, barcode, group },
    handleChange,
    setFieldTouched,
    handleBlur,
    setFieldValue,
    isValid,
    touched,
    errors,
    validateForm,
    submitForm
  } = props;

  useImperativeHandle(ref, () => ({
    isValid() {
      submitForm();
      return isValid;
    }
  }));

  const [labelWidth, setLabelWidth] = React.useState(0);
  React.useEffect(() => {
    setLabelWidth(inputLabel.current!.offsetWidth);
  }, []);

  function makeid(length: number) {
    var result = "";
    var characters =
      "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    var charactersLength = characters.length;
    for (var i = 0; i < length; i++) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
  }

  const generateCode = () => {
    setFieldValue("code", makeid(13));
  };

  return (
    <form onSubmit={() => {}}>
      <Grid container spacing={6}>
        <Grid item md={3} xs={12}>
          <Tooltip title="Informe o nome do produto">
            <TextField
              id="name"
              name="name"
              label="Nome do produto"
              variant="outlined"
              helperText={touched.name ? errors.name : ""}
              error={touched.name && Boolean(errors.name)}
              fullWidth
              value={name}
              onChange={handleChange}
            />
          </Tooltip>
        </Grid>

        <Grid item md={3} xs={12}>
          <Tooltip title="Informe o código de controle interno do produto. O mesmo pode ser inserido manualmente case exista ou pode ser gerado automaticamente ultilizando o botão ao lado do campo">
            <TextField
              id="code"
              name="code"
              label="Código do produto"
              variant="outlined"
              fullWidth
              value={code}
              onChange={handleChange}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <Button onClick={generateCode}>Gerar</Button>
                  </InputAdornment>
                )
              }}
            />
          </Tooltip>
        </Grid>

        <Grid item md={3} xs={12}>
          <Tooltip title="Informe o código de barras(EAN) do produto. Somente serão aceitos tamanhos exatos de 8, 12, 13 ou 14 caracteres númericos.">
            <TextField
              id="barcode"
              name="barcode"
              label="Código de barra"
              variant="outlined"
              fullWidth
              value={barcode}
              onChange={handleChange}
            />
          </Tooltip>
        </Grid>

        <Grid item md={3} xs={12}>
          <FormControl variant="outlined" fullWidth placeholder="Age">
            <InputLabel ref={inputLabel} htmlFor="outlined-age-simple">
              Age
            </InputLabel>
            <Select
              name="group"
              value={group}
              onChange={handleChange}
              onBlur={event => {
                const e = {
                  ...event,
                  name: "group"
                };
                handleBlur(e);
              }}
              input={
                <OutlinedInput
                  labelWidth={labelWidth}
                  name="age"
                  id="outlined-age-simple"
                />
              }
              onClick={() =>
                setTimeout(() => setGroups(["LIMPEZA", "PC", "TEST"]), 2000)
              }
            >
              {groups.map((group, index) => (
                <MenuItem value={group} key={index}>
                  {group}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid>
      </Grid>
    </form>
  );
});

const validationSchema = object({
  name: string().required("O nome é obrigatório")
});

const initialValues: FormData = {
  name: "",
  code: "",
  barcode: "",
  group: ""
};

export default forwardRef((props, ref) => {
  const formRef = useRef<{ isValid: () => boolean }>();

  useImperativeHandle(ref, () => ({
    isValid() {
      if (formRef && formRef.current) {
        return formRef.current.isValid();
      }
      return false;
    }
  }));

  return (
    <Formik
      initialValues={initialValues}
      onSubmit={() => {}}
      validationSchema={validationSchema}
      render={props => <Form {...props} ref={formRef} />}
    />
  );
});
