import React, { useRef } from "react";
import { makeStyles, Theme, createStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import Box from "@material-ui/core/Box";

// forms
import GeneralForm from "./forms/GeneralForm";

interface TabPanelProps {
  children?: React.ReactNode;
  index: any;
  value: any;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      <Box p={3}>{children}</Box>
    </Typography>
  );
}

function a11yProps(index: any) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`
  };
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,
      backgroundColor: theme.palette.background.paper
    },
    control: {
      marginTop: "16px",
      marginRight: "12px"
    }
  })
);

export default function SimpleTabs() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const generalFormRef = useRef<{ isValid: () => {} }>();

  function handleChange(_: React.ChangeEvent<{}>, newValue: number) {
    setValue(newValue);
  }

  const saveProduct = () => {
    if (generalFormRef && generalFormRef.current) {
      if (generalFormRef.current.isValid()) {
        alert("SAVED");
      }
    }
  };

  return (
    <>
      <div className={classes.root}>
        <AppBar position="static">
          <Tabs
            value={value}
            onChange={handleChange}
            aria-label="simple tabs example"
          >
            <Tab label="Dados" {...a11yProps(0)} />
            <Tab label="Detalhes" {...a11yProps(1)} />
            <Tab label="Valores" {...a11yProps(2)} />
            <Tab label="Estoque" {...a11yProps(3)} />
            <Tab label="Fotos" {...a11yProps(4)} />
            <Tab label="Fiscal" {...a11yProps(5)} />
            <Tab label="Composição" {...a11yProps(6)} disabled={true} />
            <Tab label="Fornecedores" {...a11yProps(7)} />
          </Tabs>
        </AppBar>
        <TabPanel value={value} index={0}>
          <GeneralForm ref={generalFormRef} />
        </TabPanel>
        <TabPanel value={value} index={1}>
          Item Two
        </TabPanel>
        <TabPanel value={value} index={2}>
          Item Three
        </TabPanel>
      </div>
      <Button
        variant="contained"
        color="primary"
        className={classes.control}
        onClick={saveProduct}
      >
        Salvar
      </Button>
    </>
  );
}
