import React, {
  useEffect,
  useState,
  forwardRef,
  useRef,
  useImperativeHandle
} from "react";

// FORMIK
import { Formik, FormikProps } from "formik";

// YUP
import { string, object, boolean, number } from "yup";

//  MATERIAL UI
import TextField from "@material-ui/core/TextField";
import InputAdornment from "@material-ui/core/InputAdornment";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import Tooltip from "@material-ui/core/Tooltip";
import OutlinedInput from "@material-ui/core/OutlinedInput";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import FormHelperText from "@material-ui/core/FormHelperText";
import Typography from "@material-ui/core/Typography";
import Checkbox from "@material-ui/core/Checkbox";
import FormControlLabel from "@material-ui/core/FormControlLabel";

export interface FormData {
  name: string;
  code: string;
  barcode: string;
  group: string;
  canChangeStock: boolean;
  enableInvoice: boolean;
  hasVariations: boolean;
  hasComposition: boolean;
  conversion: {
    unit1: string;
    quantity2: number;
    unit2: string;
  };
}

const Form = forwardRef((props: FormikProps<FormData>, ref) => {
  const [groups, setGroups] = useState<string[]>(["LIMPEZA", "PC", "TEST"]);
  const inputLabel = React.useRef<HTMLLabelElement>(null);

  const {
    values: {
      name,
      code,
      barcode,
      group,
      canChangeStock,
      enableInvoice,
      hasVariations,
      hasComposition,
      conversion
    },
    handleChange,
    setFieldTouched,
    handleBlur,
    setFieldValue,
    isValid,
    touched,
    errors,
    validateForm,
    submitForm,
    values,
    isInitialValid
  } = props;

  useImperativeHandle(ref, () => ({
    isValid() {
      validateForm();
      submitForm();
      return isValid || isInitialValid;
    },
    getData() {
      return values;
    }
  }));

  const [labelWidth, setLabelWidth] = React.useState(0);
  React.useEffect(() => {
    setLabelWidth(inputLabel.current!.offsetWidth);
  }, []);

  function makeid(length: number) {
    var result = "";
    var characters =
      "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    var charactersLength = characters.length;
    for (var i = 0; i < length; i++) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
  }

  const generateCode = () => {
    setFieldValue("code", makeid(13));
  };

  return (
    <form onSubmit={() => {}}>
      <Grid container spacing={6}>
        <Grid item md={3} xs={12}>
          <Tooltip title="Informe o nome do produto">
            <TextField
              id="name"
              name="name"
              label="Nome do produto"
              variant="outlined"
              helperText={touched.name ? errors.name : ""}
              error={touched.name && Boolean(errors.name)}
              fullWidth
              value={name}
              onChange={handleChange}
            />
          </Tooltip>
        </Grid>

        <Grid item md={3} xs={12}>
          <Tooltip title="Informe o código de controle interno do produto. O mesmo pode ser inserido manualmente case exista ou pode ser gerado automaticamente ultilizando o botão ao lado do campo">
            <TextField
              id="code"
              name="code"
              label="Código do produto"
              variant="outlined"
              helperText={touched.code ? errors.code : ""}
              error={touched.code && Boolean(errors.code)}
              fullWidth
              value={code}
              onChange={handleChange}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <Button onClick={generateCode}>Gerar</Button>
                  </InputAdornment>
                )
              }}
            />
          </Tooltip>
        </Grid>

        <Grid item md={3} xs={12}>
          <Tooltip title="Informe o código de barras(EAN) do produto. Somente serão aceitos tamanhos exatos de 8, 12, 13 ou 14 caracteres númericos.">
            <TextField
              id="barcode"
              name="barcode"
              label="Código de barra"
              variant="outlined"
              helperText={touched.barcode ? errors.barcode : ""}
              error={touched.barcode && Boolean(errors.barcode)}
              fullWidth
              value={barcode}
              onChange={handleChange}
            />
          </Tooltip>
        </Grid>

        <Grid item md={3} xs={12}>
          <FormControl
            variant="outlined"
            fullWidth
            error={touched.group && Boolean(errors.group)}
          >
            <InputLabel
              ref={inputLabel}
              htmlFor="outlined-group"
              error={touched.group && Boolean(errors.group)}
            >
              Grupo
            </InputLabel>
            <Select
              name="group"
              value={group}
              onChange={handleChange}
              onBlur={event => {
                const e = {
                  ...event,
                  name: "group"
                };
                handleBlur(e);
              }}
              input={
                <OutlinedInput
                  labelWidth={labelWidth}
                  name="group"
                  id="outlined-group"
                />
              }
            >
              {groups.map((group, index) => (
                <MenuItem value={group} key={index}>
                  {group}
                </MenuItem>
              ))}
            </Select>
            {touched.name && (
              <FormHelperText error>{errors.group}</FormHelperText>
            )}
          </FormControl>
        </Grid>

        <Grid item xs={6}>
          <Grid container justify="space-between">
            <Grid item xs={2}>
              <TextField
                variant="outlined"
                disabled={true}
                type="number"
                value="1"
                fullWidth
              />
            </Grid>
            <Grid item xs={9}>
              <FormControl variant="outlined" fullWidth>
                <InputLabel htmlFor="outlined-group">Unidade</InputLabel>

                <Select
                  id="conversion.unit1"
                  name="conversion.unit1"
                  value={conversion.unit1}
                  onChange={handleChange}
                  onBlur={event => {
                    const e = {
                      ...event,
                      name: "conversion.unit1"
                    };
                    handleBlur(e);
                  }}
                  input={
                    <OutlinedInput
                      labelWidth={140}
                      name="unit1"
                      id="outlined-unit"
                    />
                  }
                >
                  <MenuItem value="unit1">Unidade 1</MenuItem>
                  <MenuItem value="unit2">Unidade 2</MenuItem>
                </Select>
              </FormControl>
            </Grid>

            <Grid item xs={12}>
              <Typography align="center">EQUIVALE A</Typography>
            </Grid>

            <Grid item xs={2}>
              <TextField
                id="conversion.quantity2"
                name="conversion.quantity2"
                variant="outlined"
                type="number"
                fullWidth
                value={conversion.quantity2}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={9}>
              <FormControl variant="outlined" fullWidth>
                <InputLabel htmlFor="outlined-conversion.unit2">
                  Unidade
                </InputLabel>

                <Select
                  name="conversion.unit2"
                  id="conversion.unit2"
                  value={conversion.unit2}
                  onChange={handleChange}
                  onBlur={event => {
                    const e = {
                      ...event,
                      name: "conversion.unit2"
                    };
                    handleBlur(e);
                  }}
                  input={
                    <OutlinedInput
                      labelWidth={140}
                      name="unit2"
                      id="outlined-conversion.unit2"
                    />
                  }
                >
                  <MenuItem value="unit1">Unidade 1</MenuItem>
                  <MenuItem value="unit2">Unidade 2</MenuItem>
                </Select>
              </FormControl>
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs={6}>
          <FormControlLabel
            control={
              <Checkbox
                name="canChangeStock"
                checked={canChangeStock}
                onChange={e => handleChange({ ...e, name: "canChangeStock" })}
              />
            }
            label="Movimenta estoque"
          />

          <FormControlLabel
            control={
              <Checkbox
                name="enableInvoice"
                checked={enableInvoice}
                onChange={e => handleChange({ ...e, name: "enableInvoice" })}
              />
            }
            label="Emite nota fiscal"
          />

          <FormControlLabel
            control={
              <Checkbox
                name="hasVariations"
                checked={hasVariations}
                onChange={e => handleChange({ ...e, name: "hasVariations" })}
              />
            }
            label="Tem variações"
          />

          <FormControlLabel
            control={
              <Checkbox
                name="hasComposition"
                checked={hasComposition}
                onChange={e => handleChange({ ...e, name: "hasComposition" })}
              />
            }
            label="Tem composição"
          />
        </Grid>
      </Grid>
    </form>
  );
});

const validationSchema = object({
  name: string().required("O nome é obrigatório"),
  code: string()
    .required("O código é obrigatório")
    .length(13, "O código deve ter 13 caracteres"),
  barcode: string().required("O código de barras é obrigatório"),
  group: string().required("O grupo é obrigatório"),
  canChangeStock: boolean().required("failed"),
  enableInvoice: boolean().required("failed"),
  hasVariations: boolean().required("failed"),
  hasComposition: boolean().required("failed"),
  conversion: object({
    unit1: string().required("failed"),
    quantity2: number().required("failed"),
    unit2: string().required("failed")
  })
});

interface FormProps {
  values: FormData;
}

export default forwardRef((props: FormProps, ref) => {
  // TODO: Extract this pattern as separate type(used in too many places)
  const formRef = useRef<{ isValid: () => boolean; getData: () => any }>();

  useImperativeHandle(ref, () => ({
    isValid() {
      if (formRef && formRef.current) {
        return formRef.current.isValid();
      }
      return false;
    },
    getData() {
      if (formRef && formRef.current) {
        return {
          general: {
            ...formRef.current.getData()
          }
        };
      }
      return {
        general: {}
      };
    }
  }));
  const isInitialValid = validationSchema.isValidSync(props.values);

  return (
    <Formik
      initialValues={props.values}
      onSubmit={() => {}}
      validationSchema={validationSchema}
      isInitialValid={isInitialValid}
      render={props => <Form {...props} ref={formRef} />}
    />
  );
});
