import React from "react";
import { Route, RouteProps } from "react-router-dom";

interface IProps {
  layout: React.FC;
}

export default ({
  component,
  layout: Layout,
  ...rest
}: RouteProps & IProps) => {
  const Component = component as any;
  return (
    <Route
      {...rest}
      render={props => (
        <Layout>
          <Component {...props} />
        </Layout>
      )}
    />
  );
};
