import React from "react";
import { BrowserRouter as Router, Switch, RouteProps } from "react-router-dom";

import RouteWithLayout from "./RouteWithLayout";
import Layout from "../layout";

import { DashboardView, ProductsView } from "../views";

const MainRoute = (props: RouteProps) => (
  <RouteWithLayout {...props} layout={Layout} />
);

export default () => {
  return (
    <Router>
      <Switch>
        <MainRoute path="/" component={DashboardView} exact={true} />
        <MainRoute path="/products" component={ProductsView} />
      </Switch>
    </Router>
  );
};
